import { logerror } from 'utils/actions'
import getinstance from 'utils/api/getinstance'
import { Store, useStore } from './Store'

const hexToHSL = (H: any) => {
  // Convert hex to RGB first
  let r: any = 0,
    g: any = 0,
    b: any = 0
  if (H.length == 4) {
    r = '0x' + H[1] + H[1]
    g = '0x' + H[2] + H[2]
    b = '0x' + H[3] + H[3]
  } else if (H.length == 7) {
    r = '0x' + H[1] + H[2]
    g = '0x' + H[3] + H[4]
    b = '0x' + H[5] + H[6]
  }
  // Then to HSL
  r /= 255
  g /= 255
  b /= 255
  let cmin: any = Math.min(r, g, b),
    cmax: any = Math.max(r, g, b),
    delta: any = cmax - cmin,
    h = 0,
    s = 0,
    l = 0

  if (delta == 0) h = 0
  else if (cmax == r) h = ((g - b) / delta) % 6
  else if (cmax == g) h = (b - r) / delta + 2
  else h = (r - g) / delta + 4

  h = Math.round(h * 60)

  if (h < 0) h += 360

  l = (cmax + cmin) / 2
  s = delta == 0 ? 0 : delta / (1 - Math.abs(2 * l - 1))
  s = +(s * 100).toFixed(1)
  l = +(l * 100).toFixed(1)

  let hsl: any = { h, s, l }
  return hsl
}

const HSLToHex = (h: any, s: any, l: any) => {
  s /= 100
  l /= 100

  let c: any = (1 - Math.abs(2 * l - 1)) * s,
    x: any = c * (1 - Math.abs(((h / 60) % 2) - 1)),
    m: any = l - c / 2,
    r: any = 0,
    g: any = 0,
    b: any = 0

  if (0 <= h && h < 60) {
    r = c
    g = x
    b = 0
  } else if (60 <= h && h < 120) {
    r = x
    g = c
    b = 0
  } else if (120 <= h && h < 180) {
    r = 0
    g = c
    b = x
  } else if (180 <= h && h < 240) {
    r = 0
    g = x
    b = c
  } else if (240 <= h && h < 300) {
    r = x
    g = 0
    b = c
  } else if (300 <= h && h < 360) {
    r = c
    g = 0
    b = x
  }
  // Having obtained RGB, convert channels to hex
  r = Math.round((r + m) * 255).toString(16)
  g = Math.round((g + m) * 255).toString(16)
  b = Math.round((b + m) * 255).toString(16)

  // Prepend 0s, if necessary
  if (r.length == 1) r = '0' + r
  if (g.length == 1) g = '0' + g
  if (b.length == 1) b = '0' + b

  return '#' + r + g + b
}
export class ColourObjItem {
  color_11: String
  color_12: String
  color_13: String
  color_14: String
  color_15: String
  color_24: String
  color_red: String
  color_green: String
  color_m1: String
  color_m2: String
  color_m3: String
  color_s1: String
  color_s2: String
  color_s3: String
  color_t1: String
  color_t2: String
  color_t3: String

  constructor(o: {
    color_11: String
    color_12: String
    color_13: String
    color_14: String
    color_15: String
    color_24: String
    color_red: String
    color_green: String
    color_m1: String
    color_m2: String
    color_m3: String
    color_s1: String
    color_s2: String
    color_s3: String
    color_t1: String
    color_t2: String
    color_t3: String
  }) {
    this.color_11 = o.color_11
    this.color_12 = o.color_12
    this.color_13 = o.color_13
    this.color_14 = o.color_14
    this.color_15 = o.color_15
    this.color_24 = o.color_24
    this.color_red = o.color_red
    this.color_green = o.color_green
    this.color_m1 = o.color_m1
    this.color_m2 = o.color_m2
    this.color_m3 = o.color_m3
    this.color_s1 = o.color_s1
    this.color_s2 = o.color_s2
    this.color_s3 = o.color_s3
    this.color_t1 = o.color_t1
    this.color_t2 = o.color_t2
    this.color_t3 = o.color_t3
  }
}

export const generate_theme_colours = (value: any) => {
  // color 1 to HSL
  let m1: any = {}
  m1 = hexToHSL(value)
  // calculating color 2
  let m2h_temp: any = 0
  m2h_temp = m1.h - 12
  if (m2h_temp > 360) {
    m2h_temp = m2h_temp - 360
  } else if (m2h_temp < 0) {
    m2h_temp = m2h_temp + 360
  }

  // calculating color 3
  let m3h_temp = 0
  m3h_temp = m1.h - 170
  if (m3h_temp > 360) {
    m3h_temp = m3h_temp - 360
  } else if (m3h_temp < 0) {
    m3h_temp = m3h_temp + 360
  }

  let m2 = ''
  m2 = HSLToHex(m2h_temp, 50, 45)

  let m3 = ''
  m3 = HSLToHex(m3h_temp, 93, 54)

  let s1 = ''
  s1 = HSLToHex(m1.h, 30, 70)

  let s2 = ''
  s2 = HSLToHex(m2h_temp, 30, 70)

  let s3 = ''
  s3 = HSLToHex(m3h_temp, 30, 70)

  let t1 = ''
  t1 = HSLToHex(m1.h, 50, 95)

  let t2 = ''
  t2 = HSLToHex(m2h_temp, 50, 95)

  let t3 = ''
  t3 = HSLToHex(m3h_temp, 50, 95)
  // console.log(m2_temp, m1.s, 29);

  let data = {
    color_11: '#ad43f5',
    color_12: '#78ce6a',
    color_13: '#EC7A4A',
    color_14: '#f5d343',
    color_15: '#76c2f6',
    color_24: '#1900ff',
    color_red: '#e00000',
    color_green: '#639A4D',
    color_m1: value,
    color_m2: m2,
    color_m3: m3,
    color_s1: s1,
    color_s2: s2,
    color_s3: s3,
    color_t1: t1,
    color_t2: t2,
    color_t3: t3
  }

  // console.log(data)
  return data
}

export const colorStore = new Store<ColourObjItem | false>({
  color_11: '#ad43f5',
  color_12: '#78ce6a',
  color_13: '#EC7A4A',
  color_14: '#f5d343',
  color_15: '#76c2f6',
  color_24: '#1900ff',
  color_red: '#e00000',
  color_green: '#639A4D',
  color_m1: '#00253a',
  color_m2: '#3998ab',
  color_m3: '#f6901d',
  color_s1: '#9bb8c8',
  color_s2: '#9bc1c8',
  color_s3: '#c8b39b',
  color_t1: '#ebf3f8,',
  color_t2: '#ebf6f8',
  color_t3: '#f8f2eb'
})

export const useColourObjectStore = () => {
  const [colourObj, setcolourObj] = useStore(colorStore)

  return {
    colourObj,
    setcolourObj,

    async loadinstancecolor(id: any) {
      await getinstance(id)
        .then(data => {
          setcolourObj(generate_theme_colours(data.data.results[0]?.color_field))
        })
        .catch(err => {
          logerror(err, err.response)
        })
    },

    loaddefaultcolor() {
      setcolourObj(generate_theme_colours('#00253a'))
    },

    setinstancecolor(color: string) {
      setcolourObj(generate_theme_colours(color))
    }
  }
}
